*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  height: auto;
}

input,
button,
textarea,
select {
  font-family: inherit;
}

button {
  border: none;
  cursor: pointer;
}

textarea {
  resize: vertical;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background: #333;
  font-family: sans-serif;
  color: #fff;
}

html {
  scroll-padding-top: 50px;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #fffafa;
  color: #000;
}

.section-title {
  padding-top: 40px;
  text-align: center;
  text-transform: uppercase;
  font-size: 2.2em;
}

.active {
  font-weight: bold;
}

.navbar {
  width: 100%;
  padding: 0;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fffafa;
  z-index: 10;
}
.navbar .navbar-title {
  padding: 10px;
  text-decoration: none;
  font-size: 1.8rem;
  color: #000;
}
.navbar .navbar-title .fas {
  margin-right: 5px;
  transition: color 0.3s;
}
.navbar .navbar-title:hover > .fas {
  color: #008000;
}
.navbar .navbar-btn {
  padding: 10px;
  background: none;
  font-size: 1.8rem;
  transition: transform 0.3s;
}
.navbar .navbar-btn:hover {
  transform: scale(1.2);
}
.navbar .navbar-links {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navbar .navbar-links .nav-link {
  text-align: center;
  padding: 20px;
  font-size: 1.8rem;
}
.navbar .nav-link-return {
  text-decoration: none;
}
.navbar .nav-link-return .fa-arrow-left {
  margin-right: 5px;
}
.navbar .show-nav {
  display: flex;
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  background-color: #fffafa;
  color: #000;
  z-index: 5;
}
.navbar .navbar-links::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  height: 1px;
  width: 90%;
  transform: translateX(-50%);
  background-color: #000;
}

.header .hero-img {
  position: relative;
  height: 300px;
  background-image: url(../img/jungle-big.jpg);
  background-position: center;
  background-size: cover;
  z-index: 0;
}
.header .hero-img .hero-shadow {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: -1;
}
.header .hero-img .hero-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px;
  height: 100%;
  color: #fff;
  z-index: 0;
}
.header .hero-img .hero-text .header-info {
  margin-bottom: 5px;
  font-size: 1.4em;
}
.header .hero-img .hero-text .header-title {
  font-size: 2em;
  font-weight: bold;
}

.about-us .about-us-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.about-us .about-us-box .about-us-img {
  width: 100%;
  margin-bottom: 20px;
}
.about-us .about-us-box .about-us-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  color: #000;
}
.about-us .about-us-box .about-us-text .about-us-title {
  font-weight: bold;
}
.about-us .about-us-box .about-us-text .about-us-text {
  text-align: justify;
  line-height: 1.5;
  font-size: 1.4em;
}
.about-us .about-us-hero {
  padding-top: 30px;
}
.about-us .about-us-hero .hero-img {
  position: relative;
  height: 150px;
  background-image: url(../img/girl-mini.jpg);
  background-position: center;
  background-size: cover;
  z-index: 0;
}
.about-us .about-us-hero .hero-img .hero-shadow {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: -1;
}
.about-us .about-us-hero .hero-img .hero-text {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60%;
  transform: translate(-50%, -50%);
  border: 1px solid white;
  color: #fff;
  z-index: 0;
}
.about-us .about-us-hero .hero-img .hero-text .text {
  margin: 0;
  padding: 15px 20px;
  text-align: center;
  font-size: 1.4em;
}

.offer {
  padding: 0 40px;
}
.offer .offer-info {
  margin: 30px;
  text-align: center;
  font-size: 1.6rem;
}
.offer .offer-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
}
.offer .offer-box .offer-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.6);
  transition: transform 0.3s;
}
.offer .offer-box .offer-item .item-title {
  margin: 0;
  text-align: center;
  font-size: 2.4rem;
}
.offer .offer-box .offer-item .item-price {
  text-align: center;
  font-size: 3.4rem;
  font-weight: bold;
}
.offer .offer-box .offer-item .offer-list {
  padding: 0;
}
.offer .offer-box .offer-item .offer-list .option {
  padding: 5px 0;
  font-size: 1.4em;
  list-style: none;
}
.offer .offer-box .offer-item .offer-list .option .fas {
  margin-right: 10px;
}
.offer .offer-box .offer-item .offer-list .option .fa-check {
  color: rgb(0, 136, 0);
}
.offer .offer-box .offer-item .offer-list .option .fa-minus {
  color: rgb(201, 0, 0);
}
.offer .offer-box .offer-item .offer-btn {
  margin: 10px;
  padding: 6px 50px;
  font-size: 1.6em;
  border: none;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  transition: box-shadow 0.3s, transform 0.3s;
}
.offer .offer-box .offer-item .offer-btn:hover {
  box-shadow: 0 0 10px rgb(0, 0, 0);
  transform: scale(0.9);
}
.offer .offer-box .offer-item .recommended {
  background-color: #00fa9a;
}
.offer .offer-box .offer-item:hover {
  transform: scale(1.05);
}
.offer .offer-link {
  margin-top: 30px;
  text-align: center;
  font-size: 1.6rem;
}
.offer .offer-link a {
  transition: font-weight 0.3s, color 0.3s;
}
.offer .offer-link a:hover {
  font-weight: bold;
  color: #008000;
}

.contact {
  margin-top: 30px;
  background-color: #eee;
  border-bottom: 1px solid black;
}
.contact .contact-box {
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact .contact-box .logo-box {
  margin-bottom: 20px;
}
.contact .contact-box .logo-box .logo {
  font-size: 2.4em;
}
.contact .contact-box .logo-box .logo .fas {
  margin-right: 5px;
}
.contact .contact-box .info-box {
  display: flex;
  justify-content: center;
  gap: 30px;
}
.contact .contact-box .info-box .item .item-title {
  font-size: 1.4em;
  font-weight: bold;
  margin-bottom: 10px;
}
.contact .contact-box .info-box .item .item-text {
  font-size: 1.2em;
}

.footer {
  padding: 10px;
  background-color: #eee;
  text-align: center;
}
.footer p {
  font-size: 1.2em;
}

.bold-text {
  font-weight: bold;
}

@media (min-width: 576px) {
  .section-title {
    font-size: 2.4em;
  }
  .header .hero-img .hero-text .header-info {
    font-size: 1.8em;
  }
  .header .hero-img .hero-text .header-title {
    font-size: 2.4em;
  }
  .about-us .about-us-box .about-us-text .about-us-title {
    font-size: 2rem;
  }
  .about-us .about-us-box .about-us-text .about-us-text {
    text-align: justify;
    line-height: 1.5;
    font-size: 1.6em;
  }
  .about-us .about-us-hero .hero-img .hero-text .text {
    font-size: 1.8em;
  }
  .offer .offer-info {
    font-size: 1.8rem;
  }
  .offer .offer-link {
    font-size: 2rem;
  }
  .contact .contact-box .logo-box .logo {
    font-size: 2.6em;
  }
  .contact .contact-box .info-box .item .item-title {
    font-size: 1.8em;
  }
  .contact .contact-box .info-box .item .item-text {
    font-size: 1.4em;
  }
}
@media (min-width: 768px) {
  .section-title {
    font-size: 2.6em;
  }
  .navbar {
    height: 60px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
  .navbar .navbar-title {
    font-size: 2rem;
  }
  .navbar .navbar-btn {
    display: none;
  }
  .navbar .navbar-links {
    display: flex;
    line-height: 60px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: #000;
  }
  .navbar .navbar-links .nav-link {
    text-align: center;
    width: 120px;
    padding: 0px 20px;
    font-size: 1.6rem;
    transition: background-color 0.3s, color 0.3s;
  }
  .navbar .navbar-links .nav-link:hover {
    background-color: #000;
    color: #fff;
  }
  .navbar .navbar-links::before {
    display: none;
  }
  .navbar .nav-link-return {
    font-size: 1.4rem;
  }
  .header .hero-img .hero-text .header-info {
    font-size: 2em;
  }
  .header .hero-img .hero-text .header-title {
    font-size: 2.6em;
  }
  .about-us .about-us-hero .hero-img .hero-text .text {
    font-size: 2em;
  }
  .offer .offer-info {
    font-size: 2rem;
  }
  .offer .offer-box {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .contact .contact-box {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-evenly;
  }
  .contact .contact-box .logo-box .logo {
    font-size: 3.4em;
  }
  .contact .contact-box .info-box .item .item-title {
    font-size: 1.8em;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .contact .contact-box .info-box .item .item-text {
    font-size: 1.4em;
  }
  .footer p {
    font-size: 1.4em;
  }
}
@media (min-width: 992px) {
  .section-title {
    font-size: 2.8em;
  }
  .navbar .navbar-title {
    font-size: 2.4rem;
  }
  .navbar .navbar-links .nav-link {
    font-size: 1.8rem;
  }
  .about-us {
    background-color: #fffafa;
  }
  .about-us .about-us-box {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    padding: 20px;
  }
  .about-us .about-us-box .about-us-img {
    width: 400px;
    margin-right: 50px;
  }
  .about-us .about-us-box .about-us-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #000;
  }
  .about-us .about-us-box .about-us-text .about-us-title {
    font-size: 2.2rem;
  }
  .about-us .about-us-box .about-us-text .about-us-text {
    text-align: justify;
    line-height: 1.5;
    font-size: 1.6em;
  }
  .about-us .about-us-hero .hero-img {
    height: 200px;
  }
  .about-us .about-us-hero .hero-img .hero-text {
    width: 50%;
  }
  .about-us .about-us-hero .hero-img .hero-text .text {
    font-size: 2.2em;
  }
  .offer .offer-info {
    font-size: 2.2rem;
  }
  .offer .offer-box {
    gap: 50px;
  }
  .offer .offer-box .offer-item {
    padding: 25px;
  }
  .offer .offer-box .offer-item .item-title {
    font-size: 2.6rem;
  }
  .offer .offer-box .offer-item .item-price {
    font-size: 3.6rem;
  }
  .offer .offer-box .offer-item .offer-list .option {
    font-size: 1.6em;
  }
  .offer .offer-box .offer-item .offer-btn {
    font-size: 1.6em;
  }
  .offer .offer-link {
    font-size: 2.2rem;
  }
  .contact .contact-box .logo-box .logo {
    font-size: 3.8em;
  }
  .contact .contact-box .info-box .item .item-title {
    font-size: 2em;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .contact .contact-box .info-box .item .item-text {
    font-size: 1.6em;
  }
}
@media (min-width: 1200px) {
  .header .hero-img .hero-text .header-info {
    font-size: 2.2em;
  }
  .header .hero-img .hero-text .header-title {
    font-size: 3.2em;
  }
  .about-us .about-us-box .about-us-img {
    width: 500px;
    margin-right: 80px;
  }
  .about-us .about-us-box .about-us-text .about-us-title {
    font-size: 2.4rem;
  }
  .about-us .about-us-box .about-us-text .about-us-text {
    text-align: justify;
    line-height: 1.5;
    font-size: 1.8em;
  }
  .about-us .about-us-hero .hero-img {
    height: 250px;
  }
  .about-us .about-us-hero .hero-img .hero-text .text {
    font-size: 2.6em;
  }
  .offer .offer-info {
    font-size: 2.4rem;
  }
  .offer .offer-box {
    gap: 60px;
  }
  .offer .offer-box .offer-item {
    padding: 25px;
  }
  .offer .offer-box .offer-item .item-title {
    font-size: 2.8rem;
  }
  .offer .offer-box .offer-item .item-price {
    font-size: 3.8rem;
  }
  .offer .offer-box .offer-item .offer-list .option {
    font-size: 1.8em;
  }
  .offer .offer-box .offer-item .offer-btn {
    font-size: 1.8em;
  }
  .offer .offer-link {
    font-size: 2.4rem;
  }
  .contact .contact-box .info-box {
    gap: 50px;
  }
  .footer p {
    font-size: 1.6em;
  }
}
.page-contact {
  padding: 30px 0;
}
.page-contact .page-contact-info {
  padding: 20px;
  text-align: center;
  font-size: 1.8rem;
}
.page-contact .page-contact-info .fa-envelope {
  margin-right: 10px;
}

.page-contact-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.page-contact-box .page-contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 80%;
}
.page-contact-box .page-contact-form input,
.page-contact-box .page-contact-form textarea {
  width: 100%;
  font-size: 1.4rem;
}
.page-contact-box .page-contact-form input {
  margin-bottom: 20px;
}
.page-contact-box .page-contact-form label {
  font-size: 1.6rem;
}
.page-contact-box .sent {
  margin-top: 20px;
  padding: 5px;
  width: 100px;
  border: 1px solid black;
  border-radius: 5px;
  background: none;
  font-size: 1.4rem;
  transition: transform 0.3s;
}
.page-contact-box .sent:hover {
  transform: scale(0.95);
}
.page-contact-box .map {
  margin-top: 50px;
  width: 80%;
}

.page-offer {
  padding: 30px 0px;
}
.page-offer .page-offer-info {
  padding: 20px;
  text-align: center;
  font-size: 1.4rem;
}
.page-offer .box-items {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.page-offer .box-items .item {
  position: relative;
  height: 300px;
  width: 100%;
  background-position: center;
  background-size: cover;
}
.page-offer .box-items .item .bg-shadow {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.page-offer .box-items .item .item-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-15deg);
  text-transform: uppercase;
  font-weight: bold;
  font-size: 3.8rem;
  color: #fff;
  z-index: 1;
}
.page-offer .box-items .family {
  background-image: url(../img/family-pg-mini.jpg);
}
.page-offer .box-items .friends {
  background-image: url(../img/friends-pg-mini.jpg);
}
.page-offer .box-items .silence {
  background-image: url(../img/woman-pg-mini.jpg);
}